''
import React, { useState } from 'react';
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import NotificationsIcon from '@mui/icons-material/Notifications';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import LanguageIcon from '@mui/icons-material/Language';
import Badge from '@mui/material/Badge';
import IconButton from '@mui/material/IconButton';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import HomeIcon from '@mui/icons-material/Home';
import SettingsIcon from '@mui/icons-material/Settings';
import LogoutIcon from '@mui/icons-material/Logout';
import { CssBaseline } from '@mui/material';

export default function Dashboard() {
  const [sidebarOpen, setSidebarOpen] = useState(true); // Sidebar is open by default

  const toggleSidebar = () => {
    setSidebarOpen(!sidebarOpen);
  };

  return (
    <div className="flex">
      {/* Sidebar */}
      <div
        className={`bg-gray-800 text-white h-screen transition-all duration-300 ${
          sidebarOpen ? 'w-64' : 'w-16'
        }`}
      >
        <div className="p-4 flex justify-between items-center">
          <h1 className={`text-xl font-bold transition-opacity duration-300 ${!sidebarOpen && 'opacity-0'}`}>
            Dashboard
          </h1>
          <IconButton onClick={toggleSidebar} color="inherit">
            <MenuIcon />
          </IconButton>
        </div>
        <List>
          <ListItem button>
            <ListItemIcon>
              <HomeIcon style={{ color: 'white' }} />
            </ListItemIcon>
            {sidebarOpen && <ListItemText primary="Home" />}
          </ListItem>
          <ListItem button>
            <ListItemIcon>
              <SettingsIcon style={{ color: 'white' }} />
            </ListItemIcon>
            {sidebarOpen && <ListItemText primary="Settings" />}
          </ListItem>
          <ListItem button>
            <ListItemIcon>
              <LogoutIcon style={{ color: 'white' }} />
            </ListItemIcon>
            {sidebarOpen && <ListItemText primary="Logout" />}
          </ListItem>
        </List>
      </div>

      {/* Main Content */}
      <div className={`flex-1 transition-all duration-300 ${sidebarOpen ? 'ml-64' : 'ml-16'}`}>
        {/* Header */}
        <header className="flex items-center justify-between bg-gray-800 text-white p-4">
          {/* Left Side: Search Bar */}
          <div className="flex items-center space-x-4">
            <div className="flex items-center bg-gray-700 px-2 py-1 rounded-md">
              <SearchIcon />
              <input
                type="text"
                placeholder="Search..."
                className="bg-transparent text-white px-2 focus:outline-none"
              />
            </div>
          </div>

          {/* Right Side: Notifications, Language Selector, User Profile */}
          <div className="flex items-center space-x-6">
            {/* Notifications */}
            <IconButton color="inherit">
              <Badge badgeContent={3} color="error">
                <NotificationsIcon />
              </Badge>
            </IconButton>

            {/* Language Selector */}
            <div className="flex items-center space-x-2">
              <LanguageIcon />
              <select
                className="bg-gray-700 text-white p-2 rounded-md focus:outline-none"
                defaultValue="EN"
              >
                <option value="EN">EN</option>
                <option value="FR">FR</option>
                <option value="ES">ES</option>
              </select>
            </div>

            {/* User Profile */}
            <IconButton color="inherit">
              <AccountCircleIcon fontSize="large" />
            </IconButton>
          </div>
        </header>

        {/* Main Content Area */}
        <main className="p-4">
          <h2 className="text-2xl font-bold">Welcome to the Dashboard</h2>
          <p>This is the main content area.</p>
        </main>
      </div>
    </div>
  );
}
